"use client";

import React, { useEffect, useRef, useState, memo } from "react";
import { motion } from "framer-motion";
import { twMerge } from "tailwind-merge";
import { cn } from "@/lib/utils";
export const TextRevealCard = ({
  text,
  revealText,
  children,
  className
}: {
  text: string;
  revealText: string;
  children?: React.ReactNode;
  className?: string;
}) => {
  const [widthPercentage, setWidthPercentage] = useState(0);
  const cardRef = useRef<HTMLDivElement | any>(null);
  const [left, setLeft] = useState(0);
  const [localWidth, setLocalWidth] = useState(0);
  const [isMouseOver, setIsMouseOver] = useState(false);
  useEffect(() => {
    if (cardRef.current) {
      const {
        left,
        width: localWidth
      } = cardRef.current.getBoundingClientRect();
      setLeft(left);
      setLocalWidth(localWidth);
    }
  }, []);
  function mouseMoveHandler(event: any) {
    event.preventDefault();
    const {
      clientX
    } = event;
    if (cardRef.current) {
      const relativeX = clientX - left;
      setWidthPercentage(relativeX / localWidth * 100);
    }
  }
  function mouseLeaveHandler() {
    setIsMouseOver(false);
    setWidthPercentage(0);
  }
  function mouseEnterHandler() {
    setIsMouseOver(true);
  }
  function touchMoveHandler(event: React.TouchEvent<HTMLDivElement>) {
    event.preventDefault();
    const clientX = event.touches[0]!.clientX;
    if (cardRef.current) {
      const relativeX = clientX - left;
      setWidthPercentage(relativeX / localWidth * 100);
    }
  }
  const rotateDeg = (widthPercentage - 50) * 0.1;
  return <div onMouseEnter={mouseEnterHandler} onMouseLeave={mouseLeaveHandler} onMouseMove={mouseMoveHandler} onTouchStart={mouseEnterHandler} onTouchEnd={mouseLeaveHandler} onTouchMove={touchMoveHandler} ref={cardRef} className={cn("text-center relative overflow-hidden", className)} data-sentry-component="TextRevealCard" data-sentry-source-file="text-reveal-card.tsx">
      {children}

      <div className="relative flex text-center items-center overflow-hidden rounded-lg">
        <motion.div style={{
        width: "100%"
      }} animate={isMouseOver ? {
        opacity: widthPercentage > 0 ? 1 : 0,
        clipPath: `inset(0 ${100 - widthPercentage}% 0 0)`
      } : {
        clipPath: `inset(0 ${100 - widthPercentage}% 0 0)`
      }} transition={isMouseOver ? {
        duration: 0
      } : {
        duration: 0.4
      }} className="absolute z-20 bg-[#010219] will-change-transform" data-sentry-element="unknown" data-sentry-source-file="text-reveal-card.tsx">
          <p style={{
          textShadow: "4px 4px 15px rgba(0,0,0,0.5)"
        }} className="dark:bg-dot-white/[0.1] bg-dot-black/[0.2] text-[40px] md:text-5xl lg:text-6xl p-10 pt-2 text-center font-bold text-white text-transparent from-white to-neutral-300"
        // className="text-3xl md:text-5xl lg:text-6xl p-10 text-center font-bold text-white bg-clip-text text-transparent bg-gradient-to-b from-white to-neutral-300"
        >
            {revealText}
          </p>
        </motion.div>
        <motion.div animate={{
        left: `${widthPercentage}%`,
        rotate: `${rotateDeg}deg`,
        opacity: widthPercentage > 0 ? 1 : 0
      }} transition={isMouseOver ? {
        duration: 0
      } : {
        duration: 0.4
      }} className="h-40 w-[8px] bg-gradient-to-b from-transparent via-neutral-800 to-transparent absolute z-50 will-change-transform" data-sentry-element="unknown" data-sentry-source-file="text-reveal-card.tsx"></motion.div>

        <div className="mx-auto overflow-hidden [mask-image:linear-gradient(to_bottom,transparent,white,transparent)]">
          <p className="text-[40px] md:text-5xl lg:text-6xl p-10 pt-2 font-bold bg-clip-text text-transparent bg-[#323238]">
            {text}
          </p>
          <MemoizedStars data-sentry-element="MemoizedStars" data-sentry-source-file="text-reveal-card.tsx" />
        </div>
      </div>
    </div>;
};
export const TextRevealCardTitle = ({
  children,
  className
}: {
  children: React.ReactNode;
  className?: string;
}) => {
  return <h2 className={twMerge("text-white text-lg mb-2", className)} data-sentry-component="TextRevealCardTitle" data-sentry-source-file="text-reveal-card.tsx">
      {children}
    </h2>;
};
export const TextRevealCardDescription = ({
  children,
  className
}: {
  children: React.ReactNode;
  className?: string;
}) => {
  return <p className={twMerge("text-[#a9a9a9] text-sm", className)} data-sentry-component="TextRevealCardDescription" data-sentry-source-file="text-reveal-card.tsx">{children}</p>;
};
const Stars = () => {
  const randomMove = () => Math.random() * 4 - 2;
  const randomOpacity = () => Math.random();
  const random = () => Math.random();
  return <div className="absolute inset-0" data-sentry-component="Stars" data-sentry-source-file="text-reveal-card.tsx">
      {[...Array(80)].map((_, i) => <motion.span key={`star-${i}`} animate={{
      top: `calc(${random() * 100}% + ${randomMove()}px)`,
      left: `calc(${random() * 100}% + ${randomMove()}px)`,
      opacity: randomOpacity(),
      scale: [1, 1.2, 0]
    }} transition={{
      duration: random() * 10 + 20,
      repeat: Infinity,
      ease: "linear"
    }} style={{
      position: "absolute",
      top: `${random() * 100}%`,
      left: `${random() * 100}%`,
      width: `2px`,
      height: `2px`,
      backgroundColor: "white",
      borderRadius: "50%",
      zIndex: 1
    }} className="inline-block"></motion.span>)}
    </div>;
};
export const MemoizedStars = memo(Stars);