import { cn } from "@/lib/utils";
import { IconLayoutNavbarCollapse } from "@tabler/icons-react";
import { AnimatePresence, MotionValue, motion, useMotionValue, useSpring, useTransform } from "framer-motion";
import Link from "next/link";
import { useRef, useState } from "react";
export const FloatingDock = ({
  items,
  desktopClassName,
  mobileClassName
}: {
  items: {
    title: string;
    icon?: React.ReactNode;
    href?: string;
  }[];
  desktopClassName?: string;
  mobileClassName?: string;
}) => {
  return <>
      <FloatingDockDesktop items={items} className={desktopClassName} data-sentry-element="FloatingDockDesktop" data-sentry-source-file="floating-dock.tsx" />
      <FloatingDockMobile items={items} className={mobileClassName} data-sentry-element="FloatingDockMobile" data-sentry-source-file="floating-dock.tsx" />
    </>;
};
const FloatingDockMobile = ({
  items,
  className
}: {
  items: {
    title: string;
    icon: React.ReactNode;
    href: string;
  }[];
  className?: string;
}) => {
  const [open, setOpen] = useState(false);
  return <div className={cn("relative block md:hidden", className)} data-sentry-component="FloatingDockMobile" data-sentry-source-file="floating-dock.tsx">
      <AnimatePresence data-sentry-element="AnimatePresence" data-sentry-source-file="floating-dock.tsx">
        {open && <motion.div layoutId="nav" className="absolute bottom-full mb-2 inset-x-0 flex flex-col gap-2">
            {items.map((item, idx) => <motion.div key={item.title} initial={{
          opacity: 0,
          y: 10
        }} animate={{
          opacity: 1,
          y: 0
        }} exit={{
          opacity: 0,
          y: 10,
          transition: {
            delay: idx * 0.05
          }
        }} transition={{
          delay: (items.length - 1 - idx) * 0.05
        }}>
                <Link href={item.href} key={item.title} className="h-10 w-10 rounded-lg flex items-center justify-center backdrop-filter-blur-lg saturate-180 bg-opacity-75 bg-black-200 border border-black-300">
                  <div className="h-4 w-4">{item.icon}</div>
                </Link>
              </motion.div>)}
          </motion.div>}
      </AnimatePresence>
      <button onClick={() => setOpen(!open)} className="h-10 w-10 rounded-lg flex items-center justify-center backdrop-filter-blur-lg saturate-180 bg-opacity-75 bg-black-200 border border-black-300">
        <IconLayoutNavbarCollapse className="h-5 w-5 text-neutral-500 dark:text-neutral-400" data-sentry-element="IconLayoutNavbarCollapse" data-sentry-source-file="floating-dock.tsx" />
      </button>
    </div>;
};
const FloatingDockDesktop = ({
  items,
  className
}: {
  items: {
    title: string;
    icon: React.ReactNode;
    href: string;
  }[];
  className?: string;
}) => {
  let mouseX = useMotionValue(Infinity);
  return <motion.div onMouseMove={e => mouseX.set(e.pageX)} onMouseLeave={() => mouseX.set(Infinity)} className={cn("mx-auto hidden md:flex h-16 gap-4 items-end  rounded-2xl px-4 pb-3", className)} data-sentry-element="unknown" data-sentry-component="FloatingDockDesktop" data-sentry-source-file="floating-dock.tsx">
      {items.map(item => <IconContainer mouseX={mouseX} key={item.title} {...item} />)}
    </motion.div>;
};
function IconContainer({
  mouseX,
  title,
  icon,
  href
}: {
  mouseX: MotionValue;
  title: string;
  icon: React.ReactNode;
  href: string;
}) {
  let ref = useRef<HTMLDivElement>(null);
  let distance = useTransform(mouseX, val => {
    let bounds = ref.current?.getBoundingClientRect() ?? {
      x: 0,
      width: 0
    };
    return val - bounds.x - bounds.width / 2;
  });
  let widthTransform = useTransform(distance, [-150, 0, 150], [40, 50, 40]);
  let heightTransform = useTransform(distance, [-150, 0, 150], [40, 50, 40]);
  let widthTransformIcon = useTransform(distance, [-150, 0, 150], [20, 20, 20]);
  let heightTransformIcon = useTransform(distance, [-150, 0, 150], [20, 30, 20]);

  // let widthTransform = useTransform(distance, [-150, 0, 150], [40, 60, 40]);
  // let heightTransform = useTransform(distance, [-150, 0, 150], [40, 60, 40]);

  // let widthTransformIcon = useTransform(distance, [-150, 0, 150], [20, 30, 20]);
  // let heightTransformIcon = useTransform(
  //   distance,
  //   [-150, 0, 150], 
  //   [20, 30, 20]
  // );

  let width = useSpring(widthTransform, {
    mass: 0.2,
    stiffness: 120,
    damping: 16
  });
  let height = useSpring(heightTransform, {
    mass: 0.2,
    stiffness: 120,
    damping: 16
  });
  let widthIcon = useSpring(widthTransformIcon, {
    mass: 0.2,
    stiffness: 120,
    damping: 16
  });
  let heightIcon = useSpring(heightTransformIcon, {
    mass: 0.2,
    stiffness: 120,
    damping: 16
  });
  const [hovered, setHovered] = useState(false);
  return <Link href={href} data-sentry-element="Link" data-sentry-component="IconContainer" data-sentry-source-file="floating-dock.tsx">
      <motion.div ref={ref} style={{
      width,
      height
    }} onMouseEnter={() => setHovered(true)} onMouseLeave={() => setHovered(false)} className="aspect-square rounded-lg flex items-center justify-center relative backdrop-filter-blur-lg saturate-180 bg-opacity-75 bg-black-200 border border-black-300" data-sentry-element="unknown" data-sentry-source-file="floating-dock.tsx">
        <AnimatePresence data-sentry-element="AnimatePresence" data-sentry-source-file="floating-dock.tsx">
          {hovered && <motion.div initial={{
          opacity: 0,
          y: 10,
          x: "-50%"
        }} animate={{
          opacity: 1,
          y: 0,
          x: "-50%"
        }} exit={{
          opacity: 0,
          y: 2,
          x: "-50%"
        }} className="px-2 py-0.5 whitespace-pre rounded-md bg-gray-100 border border-zinc-200 dark:bg-neutral-800 dark:border-neutral-900 dark:text-white border-gray-200 text-neutral-700 absolute left-1/2 -translate-x-1/2 -top-8 w-fit text-xs dark:border-zinc-800">
              {title}
            </motion.div>}
        </AnimatePresence>
        <motion.div style={{
        width: widthIcon,
        height: heightIcon
      }} className="flex items-center justify-center" data-sentry-element="unknown" data-sentry-source-file="floating-dock.tsx">
          {icon}
        </motion.div>
      </motion.div>
    </Link>;
}